// Import mixins, variables
@import "mixins_var.scss";
// Layout classes
#wrapper {
  max-width: 1920px;
  margin: 0 auto;
}
.l-header {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  .logo {
    padding: 23px 0 18px 84px;
  }
  .header-menu {
    max-width: 643px;
  }
}
.l-main {
  .l-kemenger-about {
    text-align: center;
  }
}
.l-footer {
  .l-author {
    margin: 32px 0 32px;
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 5px;
    white-space: nowrap;
    span,
    a {
      font-size: 1.5rem;
    }
  }
}
